	module.exports = {"data":{"post":{"html":"<p>Guardian: <a href=\"http://www.guardian.co.uk/elsewhere/journalist/story/0,7792,634360,00.html\">Haider plays nuclear power games</a>. Today the petition drive launched by Jörg Haider's FPÖ in against the Czech nuclear plant in Temelin comes to an end. Haider has called on 1 million Austrians to sign the petition, which calls for Austria to block admission of the Czech Republic to the EU until Temelin is shut down. The petition could bring the end of the ruling coalition in Austria, as Premier Schüssel (ÖVP) has already promised not to block Czech admission.</p>","fields":{"title":"Haider And Temelin","date":"21 Jan 2002","url":"https://papascott.de/2002-01-21-haider-and-temelin/"}}},"pathContext":{"slug":"/2002-01-21-haider-and-temelin/","prev":{"fields":{"title":"Edmund and Sabine","date":"21 Jan 2002","slug":"/2002-01-21-edmund-and-sabine/"}},"next":{"fields":{"title":"Ladies Cigar Club","date":"21 Jan 2002","slug":"/2002-01-21-ladies-cigar-club/"}}}}

